import React from "react";
import { Grid, Grow, Typography } from "@mui/material";
import NewsCard from "../NewsCard/NewsCard.js";
//import useStyles from "./styles.js";
import "./Styling.css";
const infoCards = [
  {
    color: "#00838f",
    title: "Latest News",
    text: "Give me the latest news",
    height: "250px",
    textAlign:"center",
    marginLeft: "1px 12px",
    border:"solid aqua",
    borderRadius:"20px"
  },
  {
    color: "#1565c0",
    title: "News by Categories",
    info: "Business, Entertainment, General, Health, Science, Sports, Technology",
    text: "Give me the latest Technology news",
    height: "250px",
    textAlign: "center",
    position:"center",
    border:"solid navy",
    borderRadius:"20px"
  },
];

const NewsCards = ({ articles, activeArticle }) => {
  if (!articles.length) {
    return (
      <Grow in>
        <Grid  container marginLeft="25em"  alignItems="stretch" spacing={3}>
          {infoCards.map((infoCard) => (
            <Grid item xs={12} sm={6} md={4} lg={3} className="infoCard">
              <div
                className="card"
                style={{
                  backgroundColor: infoCard.color,
                  textAlign: infoCard.textAlign,
                  height: infoCard.height,
                  position:infoCard.position,
                  alignItems:infoCard.alignItems,
                  border:infoCard.border,
                  borderRadius:infoCard.borderRadius,
                  justifyContent: infoCard.justifyContent,
                }}
              >
                <Typography variant="h5" component="h5">
                  {infoCard.title}
                </Typography>
                {infoCard.info ? (
                  <Typography variant="h6" component="h6">
                    <strong>{infoCard.title.split(" ")[2]}</strong>: <br />
                    {infoCard.info}
                  </Typography>
                ) : null}
                <Typography variant="h6" component="h6">
                  Try saying: <br /> <i>{infoCard.text}</i>
                </Typography>
              </div>
            </Grid>
          ))}
        </Grid>
      </Grow>
    );
  }

  return (
    <Grow in>
      <Grid className="container" container alignItems="stretch" spacing={3}>
        {articles.map((article, i) => (
          <Grid item xs={12} sm={6} md={4} lg={3} style={{ display: "flex" }}>
            <NewsCard activeArticle={activeArticle} i={i} article={article} />
          </Grid>
        ))}
      </Grid>
    </Grow>
  );
};

export default NewsCards;
